<template>
  <div class="gauge" :class="'percent-'+percent + ' ' + 'former-'+former">
    <div class="dial">
      <div class="bar">
        <div class="circle circle-bg"></div>
        <div class="circle circle-cover"></div>
      </div>
      <div class="needle" v-if="!loading">
        <div class="inner">
        </div>
      </div>
    </div>
    <div role="status" v-if="!text" class="text">
      <span>code</span>
      <span v-text="loading ? 'Ongekend' : code"></span>
    </div>
    <div role="status" v-if="text" class="text">
      <span v-text="text"></span>
    </div>
  </div>

</template>

<script>
export default {
  name: 'gauge',
  props: {
    percent: Number,
    former: Number,
    text: String,
    loading: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    code () {
      return this.percent < 100 / 3 ? 'groen' : this.percent < (100 / 3) * 2 ? 'oranje' : 'rood';
    }
  }
};
</script>

<style scoped lang="scss">

$needle-height: 5.6rem;
$needle-top-height: 5.3rem;
$needle-width: 0.6rem;
$needle-speed: 2s;
$needle-easing: ease-out;
$circle-radius: 4.75rem;
$circle-diameter: $circle-radius * 2;
$circle-color-bg: #80CEF0;
$circle-color-cover: #fff;
$dial-stroke-width: 1.5rem;
$start-pos: -120deg;
$dial-stroke-length: 80deg;


@mixin animation {
  transform-origin: bottom center;
  animation-duration: $needle-speed;
  animation-iteration-count: 1;
  animation-timing-function: $needle-easing;
  animation-fill-mode: forwards;
}

.gauge {
  display: block;
  min-width: $circle-diameter + 1rem;
  height: $circle-diameter + 1rem;
  padding-top: .5rem;
  position: relative;
  border-radius: 50%;
  z-index: 1;

  @for $i from 0 through 100 {
    &.former-#{$i} .needle {
      transform: rotate(-(90deg - $start-pos) - (($i/100) * 2 * $start-pos));
    }
    &.percent-#{$i} .needle {
      animation-name: to-#{$i};
      @include animation;
    }
  }

  .text {
    position: absolute;
    z-index: 30;
    top: 65%;
    left: 25%;
    text-align: center;
    width: 50%;

    span {
      display: block;
      line-height: 1.5;
      text-shadow: 2px 0 0 #fff, -2px 0 0 #fff, 0 2px 0 #fff, 0 -2px 0 #fff, 1px 1px #fff, -1px -1px 0 #fff, 1px -1px 0 #fff, -1px 1px 0 #fff;

      &:last-of-type {
        text-transform: uppercase;
        font-weight: bold;
      }
    }
  }

  .needle {
    position: absolute;
    left: 0;
    right: 0;
    top: -.3rem;
    width: $needle-width;
    margin: 0 auto;
    z-index: 50;

    .inner {
      height: $needle-height;
      width: $needle-width;
      position: relative;
      transform: rotate(90deg);
      transform-origin: bottom center;
      background-image: url(data:image/svg+xml;base64,);
      background-size: 100%;
    }
  }

  .bar {
    position: relative;
    height: $circle-radius;
  }

  .circle {
    height: $circle-diameter;
    width: $circle-diameter;
    border-radius: $circle-diameter;

    margin: 0 auto;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    transform-origin: bottom center;
  }

  .circle-bg {

    background-color: $circle-color-bg;
    background: conic-gradient(from $start-pos,
        #38ab30 0 $dial-stroke-length,
        #fab600 $dial-stroke-length $dial-stroke-length*2,
        #f20f36 $dial-stroke-length*2 $dial-stroke-length*3,
        $circle-color-cover $dial-stroke-length*3 0);
    z-index: 10;
    box-shadow: 0 10px 35px -25px;
  }

  .circle-cover {
    height: $circle-diameter - $dial-stroke-width * 2;
    width: $circle-diameter - $dial-stroke-width * 2;
    border-radius: $circle-diameter;
    background: $circle-color-cover;
    margin: 0 auto;
    top: $dial-stroke-width;
    bottom: 0;
    z-index: 30;
  }
}

// Generate keyframes
@for $i from 0 through 100 {
  @keyframes to-#{$i} {
    100% {
      transform: rotate(-(90deg - $start-pos) - (($i/100) * 2 * $start-pos));
    }
  }
}
</style>
