<template>
  <main id="app">
    <div class="center">
      <h1>Is het druk in Gent?</h1>
    </div>
    <section>
      <div class="box border">
        <h2>Winkelas Veldstraat-Korenmarkt-Langemunt</h2>
        <div class="flex">
          <gauge :loading="proximusLoading" :percent="proximusPercent" :former="proximusFormer"></gauge>
          <div class="flex-7">
            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Architecto, aut commodi dignissimos eos
              explicabo
              iusto laboriosam officia officiis praesentium, quas rerum saepe suscipit voluptatem voluptatum.</p>
            <a href="https://data.stad.gent/explore/dataset/proximus-druktebarometer-info" class="small">bron:
              proximus-druktebarometer-info</a>
          </div>
        </div>
      </div>
    </section>
    <section>
      <h2>Parkeergarages</h2>

      <div class="flex">
        <div v-for="garage of parkeerGarages" :key="garage.id" class="box border flex-3">
          <h3>{{ garage.name }}<br><span class="small">{{ garage.lastupdate }}</span></h3>
          <div class="flex">
            <gauge :loading="false"
                   :text="(garage.totalcapacity - garage.availablecapacity) + ' / ' + garage.totalcapacity"
                   :percent="Math.floor(((garage.totalcapacity - garage.availablecapacity) / garage.totalcapacity)*100)"
                   :former="Math.floor(((garage.totalcapacity - garage.availablecapacity) / garage.totalcapacity)*100)"></gauge>
            <div class="flex-7">
              <p>Nog {{ garage.availablecapacity }} van de {{ garage.totalcapacity }} plaatsen
                beschikbaar.</p>
              <a href="https://data.stad.gent/explore/dataset/bezetting-parkeergarages-real-time" class="small">bron:
                bezetting-parkeergarages-real-time</a>
            </div>
          </div>
        </div>
      </div>
    </section>
    <section>
      <h2>Waar draag je een mondmasker?</h2>
      <iframe class="border" title="Staten met mondmaskerplicht"
              src="https://data.stad.gent/explore/embed/dataset/straten-mondmaskerplicht-gent/map/?location=14,51.04798,3.71432&basemap=jawg.streets&static=false&datasetcard=false&scrollWheelZoom=false"
              width="100%" height="560" frameborder="0"></iframe>
    </section>
  </main>
</template>

<script>
import Gauge from '@/components/Gauge';

export default {
  name: 'App',
  components: {Gauge},
  data () {
    return {
      proximusPercent: 0,
      proximusFormer: 0,
      proximusLoading: true,
      parkeerGarages: []
    };
  },
  methods: {
    fetchProximus () {
      let url = `https://data.stad.gent/api/records/1.0/search/?dataset=proximus-druktebarometer-info&q=&facet=description&refine.description=Winkelas+Veldstraat-Korenmarkt-Langemunt`;
      return fetch(url)
          .then(response => response.json())
          .then(({records}) => records[0])
          .then(({fields}) => fields)
          .then(({th3, th2, total_ref}) => {
            this.proximusFormer = this.proximusPercent;
            let percent;

            // th1 and th2 are both considered 'green' for now
            if (total_ref < th2) {
              let percentOfTh = total_ref / th2;
              percent = (100 / 3) * percentOfTh;
            }
            else {
              if (total_ref < th3) {
                let percentOfTh = (total_ref - th2) / (th3 - th2);
                percent = (100 / 3) + ((100 / 3) * percentOfTh);
              }
              else {
                let percentOfTh = (total_ref - th3) / th2;
                percent = (100 / 3) + (100 / 3) + ((100 / 3) * percentOfTh);
              }
            }

            this.proximusPercent = Math.floor(percent);
          })
          .then(() => this.proximusLoading = false)
          .catch(() => this.proximusLoading = true);
    },
    fetchParkeerGarages () {
      let url = `https://data.stad.gent/api/records/1.0/search/?dataset=bezetting-parkeergarages-real-time&q=`;
      return fetch(url)
          .then(response => response.json())
          .then(({records}) => {
            const garages = records.map(r => r.fields).sort((a, b) => (a.name > b.name) ? 1 : -1);
            if (garages.length) {
              this.parkeerGarages = garages;
            }
          });
    },
    fetchAll () {
      this.fetchProximus();
      this.fetchParkeerGarages();
    }
  },
  mounted () {
    this.fetchAll();
    setInterval(this.fetchAll, 30 * 60 * 1000);

  }
};
</script>

